import React from 'react';
import classNames from 'classnames';

export interface DetailNavLinkProps extends React.ComponentPropsWithoutRef<'a'> {
  readonly selected?: boolean;
  readonly disabled?: boolean;
}

export const DetailNavLink: React.FC<DetailNavLinkProps> = React.forwardRef<
  HTMLAnchorElement,
  DetailNavLinkProps
>(({ selected, disabled, className, children, ...props }, ref) => {
  return (
    <a
      ref={ref}
      className={classNames(
        'flex flex-col gap-y-3 items-center justify-center h-40 focus:outline-none focus:ring',
        {
          'bg-accent': selected === true,
          'text-dimmed2': disabled === true,
          'text-link': disabled === false,
        },
        className
      )}
      {...props}
    >
      {children}
    </a>
  );
});

DetailNavLink.displayName = 'DetailNavLink';
